body {
  background-color: #000;
}

.order-book {
  background: #222;
  width: 400px;
  color: #fff;
  margin: 50px auto;
}

.order-book .title {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px;
  box-sizing: border-box;
}

.order-book .title .depth {
  text-align: right;
}

.order-book .title h5 {
  margin: 4px 8px;
  font-size: 14px;
}

.order-book label {
  font-size: 13px;
  margin-right: 8px;
}

.order-book select {
  background: #222;
  color: #fff;
  border: none;
  outline: none;
}

.order-book table {
  width: 100%;
  color: #ddd;
  font-size: 12px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border: none;
  border-collapse: collapse;
  border-top: 1px solid #444;
}

.order-book table tr th {
  font-size: 11px;
  font-weight: bold;
  padding: 4px 8px;
  color: #999;
  text-align: right;
}

.order-book table tr td {
  padding: 4px 8px;
  text-align: right;
}

.order-book table tr td:nth-child(1) {
  width: 33.3%;
}

.order-book table tr td:nth-child(2) {
  width: 33.3%;
}

.order-book table tr td:nth-child(3) {
  width: 33.3%;
}

.order-book table .asks {
  color: #d63031;
}

.order-book table .asks tr:nth-child(2n + 1) {
  background: #333;
}

.order-book table .bids tr:nth-child(2n + 1) {
  background: #333;
}

.order-book table .market tr td {
  background: #444;
  padding: 6px 8px;
}

.order-book table .market .mark {
  color: #fdcb6e;
}

.order-book table .market svg {
  width: 16px;
  height: 16px;
  float: left;
  top: 50%;
}

.order-book table .bids {
  color: #2ed573;
}

.order-book table td.progress {
  position: relative;
}

.order-book table td.progress div {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 0;
  width: 20%;
}

.order-book table .bids td.progress div {
  background-color: rgba(255, 0, 0, 0.15);
}

.order-book table .asks td.progress div {
  background-color: rgba(0, 255, 0, 0.15);
}
